import React, { useState, useRef, Component } from 'react';
import Axios from 'axios';
import fetch from 'node-fetch'
import './CreatePost.css'
import {Navigate} from 'react-router-dom';



class CreatePost extends Component{
    state = {
        image: '',
        title: '',
        username: '',
        description: '',
        toHome: false
    }
    
    
    handleChange(event) {
        const value = event.target.value;

        this.setState({
            [event.target.id]: value
        });
    };

    imageChange = event => {
        console.log(event.target.files[0])
        this.setState({ image: event.target.files[0]}, ()=>{
            console.log(this.state.image)
        })
    }

    handleSubmit = async (event) => {
        event.preventDefault()
    
        console.log('final: ', this.state.image)
        const imgFile = this.state.image
    
        console.log("imgfile = ", imgFile)

        console.log("user ", this.state.username)
        //get aws s3 bucket url
        const {url} = await fetch('http://photohookappmain-env.eba-bz32tggm.us-east-2.elasticbeanstalk.com/s3Url').then (res=> res.json())
        console.log(url)
    
        let axiosConfig = {
            headers:{
                'Content-Type': "multipart/form-data"
            }
        }
    
        await fetch (url, {
            method: "PUT",
            headers: {
                "Content-Type": "multipart/form-data"
            },
            body: imgFile
        })
    
        console.log(imgFile.name)

        const imageUrl = url.split('?')[0]

        console.log(imageUrl)
    
        const uploadDate = new Date().toISOString();

        console.log("imageUrl " + imageUrl + "\ntitle " + this.state.title
        + "\nusername " + this.state.username + "\ndesc: " + this.state.description + "\ndate " + uploadDate)

        let reqTitle = this.state.title;
        let reqUser = this.state.username;
        let reqDesc = this.state.description;
        
        const reqOptions = {
            method: 'POST',
            headers: { 'Content-Type': 'application/json'},
            body: JSON.stringify({
                image: imageUrl,
                title: reqTitle,
                username: reqUser,
                description: reqDesc,
                date: uploadDate
            })
        }


        const response = await fetch('http://photohookappmain-env.eba-bz32tggm.us-east-2.elasticbeanstalk.com/upload', reqOptions);
        
        const data = await response;

        this.setState(({ toHome: true }))
   }
    
   


  
    render() {
        if (this.state.toHome) {
            return <Navigate to='/' />
          }

        return (
            <div className='upload-container'>
                <h2>Create A Post</h2>
                <form id="uploadForm" onSubmit={this.handleSubmit} method="POST" encType="multipart/form-data">
                    
                    
                    <label>Image</label>
                    <input type="file" accept="image/*" id="imageInput" name="imageInput" onChange={this.imageChange}/>
                
                    <label>Title</label>
                    <input type="text" id="title" name="title" value={this.state.title} onChange={this.handleChange.bind(this)} maxLength="35" />
                
                    <label>Username</label>
                    <input type="text" id="username" name="username" value={this.state.username} onChange={this.handleChange.bind(this)} maxLength="25"/>
                
                    <label className="upload-desc">Description</label>
                    <textarea id="description" name="upload-desc" value={this.state.description} onChange={this.handleChange.bind(this)} maxLength="55"/>
                        
                    <button type="submit" id="upload-btn">Upload</button>
                </form>
            </div>
        );
    }
}


export default CreatePost;
