import axios from 'axios';
import React, {Component} from 'react';
import Post from "./Post"
import "./Gallery.css";

class Gallery extends Component {
    state = {
        posts: [],
    }

    componentDidMount(){
        this.fetchPosts();
        
    }

    fetchPosts() {
        axios.get('http://photohookappmain-env.eba-bz32tggm.us-east-2.elasticbeanstalk.com/posts')
        .then(response=> {
            this.setState({ posts: response.data});
        })
        .catch(error => {
            console.error(error);
        });

        
    }

    convertDates(){
        for (let i = 0; i < this.state.posts.length; i++){
            var utcDate = this.state.posts[i].post_date;
            var localDate = new Date(utcDate).toLocaleString();
            this.state.posts[i].post_date = localDate;
        }
    }

    render() {
        this.convertDates();
        return (
            <div className="gallery-container">
                <h2>Community Photos</h2>
                <div className='gallery-posts'>
                    {this.state.posts.map(post=> (
                        <Post key={post.id} time={post.post_date.toLocaleString()} title={post.title} image={post.imagelink} username={post.username} description={post.description} />
                    ))}
                </div>
            </div>
        );
    }
}

export default Gallery;
